.SubmitBtn {
border: none;
color: white;
padding: 15px 32px;
text-align: center;
text-decoration: none;
display: inline-block;
font-size: 16px;
margin: -25% 5%;
cursor: pointer;
top: 3.6%;
left: 90%;
}

.ResetBtn {
border: none;
color: rgb(0, 0, 0);
padding: 15px 32px;
text-align: center;
text-decoration: none;
display: inline-block;
font-size: 16px;
margin: -1000% 10%;
cursor: pointer;
top: 4.8%;
left:50%;
}

.ChatInput {
transform: translate(-50%, -50%);
position: absolute;
border: 5px;
color: white;
padding: 50% 12%;
text-align: center;
text-decoration: none;
display: inline-block;
font-size: 16px;
margin: 4px 2px;
cursor: pointer;
width: 100%;
}

.bgcolour {
  background-color: rgb(0, 0, 0);
}

.text {
  background-color: #00777b;
}